/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}
@mixin small-desk {
  @media (max-width: (1100px)) {
    @content;
  }
}
@mixin g-tablet {
  @media (max-width: (840px)) {
    @content;
  }
}
@mixin d-tablet {
  @media (max-width: (969px)) {
    @content;
  }
}
@mixin s-tablet {
  @media (max-width: (669px)) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin m-mobile {
  @media (max-width: (340px)) {
    @content;
  }
}
